@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;

    font-family: 'Poppins', sans-serif;
}
:root {
    --background: #ffffff;
    --text-primary: #000;
   
  }
  
  [data-theme='dark'] {
    --background: #000;
    --text-primary: #fff;
   
  }

  body{
    background-color: var(--background);
  }